<template>
	<div class="split-container">
        <div class="split left">
        	<div class="split-text-view">
	        	<h1 class="animated flash">(UTAQI)</h1>
	            <p><strong></strong><strong></strong></p>
	            <a href="http://www.utaqi.com/#/stu" class="button"></a>
	        </div>
        </div>
        <div class="split right">
        	<div class="split-text-view">
        		<h1 class="animated flash"></h1>
	            <p>150300</p>
	            <a href="http://www.utaqi.com/#/exam" class="button"></a>
        	</div>
        </div>
    </div>
</template>

<script>
	export default{
		name:"split-view"
	}
</script>

<style>
	:root{
		--container-bg-color:#333;
	    --left-bg-color:rgba(0,0,0,0.5);
	    --right-bg-color:rgba(0,0,0,0.6);
	    --left-button-hover-bg-color:rgba(161,11,11,0.3);
	    --right-button-hover-bg-color:rgba(9,63,112,0.3);
	    --hover-width:75%;
	    --other-width:25%;
	    --speed:1000ms;
	}
	.split-container{
	    position: relative; 
	    width: 100%;
	    height: 500px;
	    background-color: var(--container-bg-color);
	}

	.split-text-view{
	    position: absolute;
	    width: 100%;
	    height: 100%;
	    overflow: hidden;
	}

	.split-text-view p{
		color: #fff;
		padding: 20px;
		margin-top:140px;
		font-size: 1.8rem;
		letter-spacing: 1px;
		text-align: justify;
		line-height: 1.8;
	}

	.split{
	    position: absolute;
	    width: 50%;
	    height: 100%;
	    overflow: hidden;
	}

	.split.left{
	    left: 0;
	    background: url("./../assets/left.jpg") center center no-repeat;
	    background-size: cover;
	    box-sizing: border-box;
	}

	.split.left::before{
	    position: absolute;
	    content: "";
	    width: 100%;
	    height: 100%;
	    background: var(--left-bg-color);
	}

	.split.right{
	    right: 0;
	    background: url("./../assets/right.jpg") center center no-repeat;
	    background-size: cover;
	}

	.split.right::before{
	    position: absolute;
	    content: "";
	    width: 100%;
	    height: 100%;
	    background: var(--right-bg-color);
	}

	.split-text-view h1{
	    font-size: 4rem;
	    letter-spacing: 1px;
	    font-weight: 300;
	    color: #fff;
	    position: absolute;
	    left: 50%; 
	    transform: translateX(-50%);
	    white-space: nowrap;
	    top:40px;
	}

	.button{
	    display: block;
	    box-sizing: border-box;
	    position: absolute;
	    left: 50%;
	    transform: translateX(-50%);
	    bottom:80px;
	    /*height: 2.5rem;*/
	    width: 15rem;
	    padding: 1rem;
	    text-align: center;
	    color: #fff;
	    border: #fff solid 0.1rem;
		border-radius: 0.4rem;
	    font-size: 1.5rem;
	    font-weight: bold;
	    text-decoration: none;
		background: rgba(0,0,0,0.5);
	}

	.split.left .button:hover{
	    background: var(--left-button-hover-bg-color);
	    border-color: var(--left-button-hover-bg-color);
	    color: #fff;
	}

	.split.right .button:hover{
	    background: var(--right-button-hover-bg-color);
	    border-color: var(--right-button-hover-bg-color);
	    color: #fff;
	}

	/*  */
	.split.left, .split.right, .split.right::before, .split.left::before{
	    transition: var(--speed) all ease-in-out;
	}

	/* hover */
	.hover-left .left{
	    width: var(--hover-width);
	}
	.hover-left .right{
	    width: var(--other-width);
	}
	.hover-left .right::before{
	    z-index: 2;
	}

	.hover-right .right{
	    width: var(--hover-width);
	}
	.hover-right .left{
	    width: var(--other-width);
	}
	.hover-right .left::before{
	    z-index: 2;
	}

	/*  */
	@media(max-width:800px){
	    h1{
	        font-size:4rem;
	    }
	    .button{
	        width:12rem;
		    padding: 0.8rem;	        
	    }
	    .split-text-view p{
	    	font-size: 1.5rem;
	    	line-height: 2;
	    }
	    .split-container{
		    height: 1000px;
		}
	    .split{
		    position: absolute;
		    width: 100%;
		    height: 100%;
		    overflow: hidden;
		}
		.split.left{
			height: 500px;
		}
		.split.right{
		    right: 0;
		    top: 500px;
		    height: 500px;
		    /*background: url("./../assets/right.jpg") center center no-repeat;
		    background-size: cover;*/
		}
		.button{
			bottom: 60px;
		}
	}
</style>


